@media only screen and (max-width:950px) {

    html, body, #app, #app>div {   
        width: 95%;
        background-color: #001318;
        color: white;
        margin-left: auto;
        margin-right: auto;
        font-family: Arial, Arial, Helvetica, sans-serif;
        font-size: 20px;
    }

    .topNavBar {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 5px;
        margin-top: .5rem;
        background-color: #08242c;
        border-radius: 5px;
    }
    
    .infoContainer {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .gridContainer {
        display: flex;
        flex-direction: column;
    }

    .rows {
        display: flex;
        flex-direction: column;
    }
    
    .infoItems {
        border: none;
        resize: none;
        color: white;
    }

   
    
    .workButton {
        cursor: pointer;
        width: 100%;
        background-color: rgb(4, 37, 65);
        color : white;
        border-radius: 8px;
    }
    
    .workButton:hover {
        background-color: rgb(17, 65, 104);
    }
    
    .navButton {
        cursor: pointer;
        height: 40px;
        width: 30%;
        background-color: #001318;
        color : white;
        border-radius: 5px;
    }
    
    .navButton:hover {
        background-color: #006443;
    }

    img {
        margin-top: 2rem;
        margin-left: auto;
        margin-right: auto;
        border: 4px solid black;
        border-top-left-radius: 25px;
        border-bottom-right-radius: 25px;
        width: 300px;
        height: 400px;
    }

    .videos {
        width: 100%;
        height: 250px;
    }

    a {
        color: white
    }
    
}

@media only screen and (min-width: 951px) {

    html, body, #app, #app>div {  
        width: 80%;
        height: 100%;
        background-color: #001318;
        color: white;
        margin-left: auto;
        margin-right: auto;
        font-family: Arial, Arial, Helvetica, sans-serif;
        font-size: 25px;
    }

    .topNavBar {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 5px;
        margin-top: .5rem;
        background-color: #08242c;
        border-radius: 5px;
    }
    
    .infoContainer {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .rows {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }
    
    .infoItems {
        border: none;
        resize: none;
        color: white;
    }

    img {
        margin-top: 2rem;
        margin-left: auto;
        margin-right: auto;
        border: 4px solid black;
        border-top-left-radius: 20px;
        border-bottom-right-radius: 20px;
        width: 300px;
        height: 400px;
    }
 
    .workButton {
        cursor: pointer;
        width: 300px;
        background-color: rgb(4, 37, 65);
        color : white;
        border-radius: 8px;
    }
    
    .workButton:hover {
        background-color: rgb(17, 65, 104);
    }
    
    .navButton {
        cursor: pointer;
        height: 40px;
        width: 20%;
        background-color: #001318;
        color : white;
        border-radius: 5px;
    }
    
    .navButton:hover {
        background-color: #006443;
    }

    .gridContainer {
        display: grid;
        grid-template-columns: 40% 40%;
        justify-content: space-around;
    }

    .videos {
        height: 425px;
        width: 660px;
    }

    a {
        color: white
    }
    
    a:hover {
        color: rgb(0, 0, 0)
    }
} 


.center {
    text-align: center;
}

.separator {
    height: 50px;
}

.iconStyle {
    width: 50px;
    height: 50px;
}
